<template>
  <div class="course" v-if="!paymentTime">
    <Modal v-show="isModalOpen" @action="generate" @cancel="hideModal" />
    <Header
      :step="step"
      @onStep="changeStep"
      :isResumeAvaible="isResumeAvaible"
    />
    <div class="course__content">
      <div class="course__nav course__nav--prev">
        <div
          class="nav__text"
          :class="{ 'course__nav--empty': step == 0 }"
          @click="changeStep(step - 1)"
        >
          <template v-if="step > 0">
            &#171;
          </template>
        </div>
      </div>
      <swiper
        ref="swiper"
        :options="swiperOption"
        @transitionEnd="onTransitionEnd"
      >
        <swiper-slide class="no-swipe">
          <Step0 @onDuration="onDuration" :duration="duration" />
        </swiper-slide>
        <swiper-slide class="no-swipe">
          <Step1 v-model="selectedTopics" />
        </swiper-slide>
        <swiper-slide class="no-swipe">
          <Step2
            :activities="activities"
            :duration="duration"
            v-model="selectedAcitvities"
          />
        </swiper-slide>
        <swiper-slide class="no-swipe">
          <Step3 :selectedAcitvities="selectedAcitvities" @onSave="onSave" />
        </swiper-slide>
      </swiper>
      <div class="course__nav course__nav--next">
        <div class="nav__text" @click="changeStep(step + 1)">
          <template v-if="isNextVisible">
            &#187;
          </template>
        </div>
      </div>
    </div>
  </div>
  <div v-else>
    <Payment />
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css';
import Header from './components/Header';
import Step0 from './components/Step0';
import Step1 from './components/Step1';
import Step2 from './components/Step2';
import Step3 from './components/Step3';
import Modal from './components/Modal';
import Payment from './components/Payment';

import { generateDoc } from './generateDoc';
import { generatePdf } from './generatePdf';
import moment from 'moment';
export default {
  name: 'App',
  components: {
    Header,
    Step0,
    swiper,
    swiperSlide,
    Step1,
    Step2,
    Step3,
    Modal,
    Payment
  },
  computed: {
    swiper() {
      return this.$refs.swiper.swiper;
    },
    isNextVisible() {
      if (this.step === 0) {
        return this.duration !== 0;
      }
      if (this.step === 1) {
        return !!this.selectedTopics.length;
      }
      if (this.step === 2) {
        return this.selectedAcitvities.every(el => !!el.activities.length);
      }
      return false;
    },
    isResumeAvaible() {
      return (
        !!this.selectedTopics.length &&
        this.selectedAcitvities.length &&
        this.selectedAcitvities.every(
          el => el.activities && !!el.activities.length
        )
      );
    }
  },
  data() {
    return {
      duration: 0,
      step: 0,
      selectedTopics: [],
      activities: [],
      selectedAcitvities: [],
      isModalOpen: false,
      paymentTime: false,
      swiperOption: {
        spaceBetween: 100,
        speed: 500,
        noSwiping: true,
        noSwipingClass: 'no-swipe'
      }
    };
  },
  created() {
    const date = moment('2020-10-01', 'YYYY-MM-DD').toISOString();
    const current = moment();
    if (current.isAfter(date)) {
      this.paymentTime = true;
    }
  },
  methods: {
    onSave() {
      this.isModalOpen = true;
    },
    hideModal() {
      this.isModalOpen = false;
    },
    generate(type) {
      if (type === 'doc') {
        generateDoc(this.selectedAcitvities);
      }
      if (type === 'pdf') {
        generatePdf(this.selectedAcitvities);
      }
    },
    onTransitionEnd() {
      this.step = this.swiper.activeIndex;
    },
    changeStep(step) {
      if (this.duration === 0) {
        return;
      }
      let nextStep = step;
      if (nextStep > 3) {
        nextStep = 3;
      }
      if (nextStep < 0) {
        nextStep = 0;
      }
      this.swiper.slideTo(nextStep, 500);
      this.step = nextStep;
    },
    onDuration(duration) {
      this.duration = duration;
      this.selectedAcitvities = this.generateActivities();
      this.swiper.slideTo(1, 500);
    },
    generateActivities() {
      const lastActivity = { title: 'Closing session', draggableSecond: true };
      const firstActivity = { title: 'Opening Session', draggableFirst: true };
      const days = Math.ceil(this.duration);
      let arr = [];
      for (let index = 1; index <= days; index++) {
        const generate = () => {
          let act = [];
          if (index === 1) {
            act = [firstActivity];
          }
          if (index === days) {
            act = [...act, lastActivity];
          }
          return act;
        };
        arr = [
          ...arr,
          {
            day: index,
            isHalf: index < days ? false : !Number.isInteger(this.duration),
            activities: generate()
          }
        ];
      }
      return arr;
    }
  },
  watch: {
    selectedTopics: {
      deep: true,
      handler(topics) {
        this.activities = topics
          .map(el => el.activities)
          .reduce((acc, el) => acc.concat(el), []);
      }
    }
  }
};
</script>

