import jsPDF from 'jspdf';
import 'jspdf-autotable';
import { range } from './generateDoc';

function generateRow(arr, index, time) {
  const styles = { minCellHeight: 25 };
  const result = arr
    .map(el =>
      el.activities.map((o, i) => {
        const obj = { styles };
        if (el.activities.length <= index) {
          return (obj.content = '');
        }

        let text = o.title;
        if (o.text) {
          text = `${text} \n${o.text}`;
        }
        if (o.parent) {
          text = `${o.parent} \n${text}`;
        }
        obj.content = text;
        return i === index ? obj : false;
      })
    )
    .reduce((acc, el) => acc.concat(el), [])
    .filter(Boolean);

  return [{ content: time, styles }, ...result];
}
