const generateData = data => {
  const times = [
    '9:00',
    '10:30',
    '11:00',
    '12:30',
    '14:00',
    '15.30',
    '16:00',
    '17.30',
    '18:00',
    '19:30',
    '20:00',
    '21:30',
    '22:00',
    '23:30'
  ];
  let i = 0;
  const arr = [];
  for (let index = 0; index < data[0].activities.length; index++) {
    const activitiesLength = data[0].activities.length;
    const row = generateRow(data, index, times[i]);
    arr.push(row);
    i++;
    let breakRow = generateBreak('Coffe break', times[i], data);
    if (times[i] === '12:30') {
      breakRow = generateBreak('Lunch break', times[i], data);
    }
    if (activitiesLength - 1 === index) {
      breakRow = generateBreak('End of day', times[i], data);
    }
    i++;
    arr.push(breakRow);
  }
  return arr;
};
function createHeaders(count) {
  const headings = ['TIME', 'DAY 1', 'DAY 2', 'DAY 3'];
  var result = [];
  for (var i = 0; i < count + 1; i += 1) {
    result.push({
      content: headings[i],
      styles: { halign: 'center' }
    });
  }
  return result;
}
function generateBreak(type, time, main) {
  const count = main.length;
  let arr = range(1, count, type);
  return [time, ...arr];
}

export const generatePdf = async data => {
  const doc = new jsPDF();

  doc.autoTable({
    theme: 'plain',
    includeHiddenHtml: true,
    styles: { fillColor: '#fff', lineWidth: 0.2, lineColor: '#000' },
    bodyStyles: { fillColor: '#fff' },
    head: [createHeaders(data.length)],
    body: generateData(data)
  });
  doc.save('course-designer.pdf');
};
