<template>
  <div class="c-topic-list-cnt">
    <div class="c-topic-list">
      <draggable
        v-model="list"
        @input="onList"
        group="people"
        class="c-topc-list__list"
        @choose="onListChoose"
        @end="onListEnd"
        :class="{ 'c-topic-list--dragging': isSelectedDragging }"
      >
        <div
          class="c-topic-list__item"
          v-for="(topic, index) in list"
          :key="index"
        >
          <div class="topic">
            <div v-if="topic.title" class="topic__title">
              {{ $t(topic.title) }}
            </div>
            <div class="topic__text">{{ topic.text }}</div>
            <a
              :href="topic.link"
              v-if="topic.link"
              class="topic__link"
              target="_blank"
              v-html="icon"
            ></a>
          </div>
        </div>
      </draggable>
    </div>
    <!-- without days -->
    <div class="c-topic__dropzone" v-if="!days">
      <div class="dropzone__title">Drop your choices below</div>
      <draggable
        v-model="selected"
        group="people"
        class="dropzone-cnt"
        :class="{ 'dropzone-cnt--dragging': isListDragging }"
        @choose="onSelectedChoose"
        @end="onSelectedEnd"
      >
        <div
          class="c-topic-list__item"
          v-for="(topic, index) in selected"
          :key="index"
        >
          <div class="topic">
            <div v-if="topic.title" class="topic__title">
              {{ $t(topic.title) }}
            </div>
            <div class="topic__text">{{ topic.text }}</div>
            <a
              :href="topic.link"
              v-if="topic.link"
              class="topic__link"
              target="_blank"
              v-html="icon"
            ></a>
          </div>
        </div>
      </draggable>
    </div>
    <!-- without days end -->
    <div class="c-topic__days" v-if="days">
      <div
        class="c-topic__dropzone"
        v-for="(day, index) in selected"
        :key="index"
      >
        <div class="dropzone__title">{{ $t('Day') }}: {{ day.day }}</div>
        <draggable
          v-model="day.activities"
          @input="onDayActivity($event, index)"
          group="people"
          class="dropzone-cnt"
          :class="{
            'dropzone-cnt--dragging':
              isListDragging ||
              (draggingIndex !== null && draggingIndex !== index)
          }"
          @choose="onSelectedChooseDay(index)"
          @end="onSelectedEnd"
        >
          <div
            class="c-topic-list__item"
            v-for="(topic, index) in day.activities"
            :key="index"
          >
            <div class="topic">
              <div v-if="topic.title" class="topic__title">
                {{ $t(topic.title) }}
              </div>
              <div class="topic__text">{{ topic.text }}</div>
              <a
                :href="topic.link"
                v-if="topic.link"
                class="topic__link"
                target="_blank"
                v-html="icon"
              ></a>
            </div>
          </div>
        </draggable>
      </div>
    </div>
    <!-- days -->
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import _ from 'lodash';
import icon from '@/assets/img/info-circle-solid.svg';

function checkIfExist(activities, values) {
  return activities
    .map(el => {
      if (el.title === 'Closing session' || el.title === 'Opening Session') {
        return el;
      }
      return values.some(o => o.title === el.title && o.text === el.text)
        ? el
        : null;
    })
    .filter(Boolean);
}

export default {
  components: { draggable },
  props: {
    value: {
      required: true,
      default() {
        return [];
      }
    },
    valuesToSelect: {
      default() {
        return [];
      }
    },
    days: {
      type: Boolean,
      default: false
    },
    duration: {}
  },
  computed: {
    selected: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit('input', val);
      }
    }
  },
  created() {
    this.list = this.valuesToSelect;
  },
  data() {
    return {
      myArr: [],
      list: [],
      isDesktop: false,
      isListDragging: false,
      isSelectedDragging: false,
      draggingIndex: null,
      icon
    };
  },
  watch: {
    valuesToSelect: {
      deep: true,
      handler() {
        this.list = this.compareValuesToSelect();
      }
    },
    duration() {
      this.list = this.valuesToSelect;
    },
    list: {
      deep: true,
      handler() {
        if (this.days) {
          const activities = this.value
            .map(el => el.activities)
            .reduce((acc, el) => acc.concat(el), []);
          const isElemntInList = this.list.some(item =>
            activities.some(
              el => el.text === item.text && el.title === item.title
            )
          );

          if (isElemntInList) {
            this.list = this.list.filter(el => {
              const founded = activities.find(
                o => o.text === el.text && el.title === o.title
              );
              if (founded) {
                return false;
              }
              return true;
            });
          }
        }
      }
    }
  },
  methods: {
    compareValuesToSelect() {
      const clone = _.cloneDeep(this.selected).map(el => ({
        ...el,
        activities: checkIfExist(el.activities, this.valuesToSelect)
      }));
      this.$emit('input', clone);
      if (!this.days || !this.list.length) {
        return this.valuesToSelect;
      }

      const elements = this.value
        .map(el => el.activities)
        .reduce((acc, el) => acc.concat(el), []);

      return _.differenceWith(this.valuesToSelect, elements, _.isEqual);
    },
    onList(e) {
      this.list = e;
    },
    onListChoose() {
      this.isListDragging = true;
    },
    onListEnd() {
      this.isListDragging = false;
      this.draggingIndex = null;
    },
    onSelectedChoose() {
      this.isSelectedDragging = true;
    },
    onSelectedEnd() {
      this.isSelectedDragging = false;
      this.draggingIndex = null;
    },
    onSelectedChooseDay(index) {
      this.isSelectedDragging = true;
      this.draggingIndex = index;
    },
    onSelectedEndDay() {
      this.isSelectedDragging = false;
    },
    onDayActivity(e, indeks) {
      const cloneSelected = _.cloneDeep(this.value);
      const clone = _.cloneDeep(e);
      // const removeLastItem = () => {
      //   const el = clone[clone.length - 2];
      //   this.$nextTick().then(() => {
      //     this.isListDragging = false;
      //     this.list = [...this.list, el];
      //   });
      //   cloneSelected[indeks].activities = clone.filter(
      //     (el, i) => i !== clone.length - 2
      //   );
      // };
      let lastItemIndex;
      if (indeks === 0 || indeks === this.value.length - 1) {
        clone.sort(a => {
          if (a.draggableFirst) return -1;
          return 0;
        });
        lastItemIndex = clone.findIndex(el => el.draggableSecond);
        if (lastItemIndex >= 0) {
          clone.push(clone.splice(lastItemIndex, 1)[0]);
        }
        cloneSelected[indeks].activities = clone;
      }

      // if (clone.length > 4) {
      //   if (lastItemIndex >= 0) {
      //     removeLastItem();
      //   }
      // }
      // if (cloneSelected[indeks].isHalf) {
      //   if (clone.length > 3) {
      //     removeLastItem();
      //   }
      // } else {
      //   if (clone.length > 4) {
      //     removeLastItem();
      //   }
      // }
      this.$emit('input', cloneSelected);
    }
  }
};
</script>

