import {
  Document,
  Packer,
  Paragraph,
  TextRun,
  Table,
  TableRow,
  TableCell
} from 'docx';
import { saveAs } from 'file-saver';

const headerRow = days => {
  return new TableRow({
    tableHeader: true,
    height: {
      rule: 'atLeast',
      height: 550
    },
    children: [
      new TableCell({
        verticalAlign: 'center',
        width: { type: 'dxa', size: 1000 },
        children: [
          new Paragraph({
            alignment: 'center',
            children: [
              new TextRun({
                text: 'TIME',
                bold: true
              })
            ]
          })
        ]
      }),
      ...days.map(el => {
        return new TableCell({
          verticalAlign: 'center',
          width: { type: 'dxa', size: 2500 },
          children: [
            new Paragraph({
              alignment: 'center',
              children: [
                new TextRun({
                  text: `Day ${el.day}`,
                  bold: true
                })
              ]
            })
          ]
        });
      })
    ]
  });
};
const dayCell = obj => {
  let title, text, parent;
  if (obj.parent) {
    parent = new Paragraph({
      children: [
        new TextRun({
          bold: true,
          underline: true,
          text: `${obj.parent}:`
        })
      ]
    });
  }

  if (obj.title) {
    title = new Paragraph({
      children: [
        new TextRun({
          bold: true,

          text: obj.title
        })
      ]
    });
  }
  if (obj.text) {
    text = new Paragraph({
      text: obj.text
    });
  }
  return new TableCell({
    margins: {
      left: 100,
      top: 100
    },
    children: [parent, title, text]
  });
};
export function range(start, end, type) {
  var foo = [];
  for (var i = start; i <= end; i++) {
    foo.push(type);
  }
  return foo;
}
const generateBreakRow = (type, time, main) => {
  const count = main.length;
  let arr = range(1, count, type);

  return new TableRow({
    height: {
      rule: 'atLeast',
      height: 400
    },
    children: [time, ...arr].map(el => dayCell({ text: el }))
  });
};
const generateRow = (arr, index, time) => {
  return new TableRow({
    height: {
      rule: 'atLeast',
      height: 1900
    },
    children: [
      new TableCell({
        margins: {
          left: 100,
          top: 100
        },
        children: [
          new Paragraph({
            text: time
          })
        ]
      }),
      ...arr
        .map(el =>
          el.activities.map((o, i) => {
            if (el.activities.length <= index) {
              return { title: '' };
            }
            return i === index ? o : false;
          })
        )
        .reduce((acc, el) => acc.concat(el), [])
        .filter(Boolean)
        .map(el => {
          return dayCell(el);
        })
    ]
  });
};
function addMissing(params, count) {
  const arr = [];
  for (let index = 0; index < count; index++) {
    if (params[index]) {
      arr.push(params[index]);
    } else {
      arr.push({});
    }
  }
  return arr;
}
export const generateAllRows = data => {
  const times = [
    '9:00',
    '10:30',
    '11:00',
    '12:30',
    '14:00',
    '15.30',
    '16:00',
    '17.30',
    '18:00',
    '19:30',
    '20:00',
    '21:30',
    '22:00',
    '23:30'
  ];
  let i = 0;
  const arr = [];
  for (let index = 0; index < data[0].activities.length; index++) {
    const activitiesLength = data[0].activities.length;
    const row = generateRow(data, index, times[i]);
    arr.push(row);
    i++;
    let breakRow = generateBreakRow('Coffe break', times[i], data);
    if (times[i] === '12:30') {
      breakRow = generateBreakRow('Lunch break', times[i], data);
    }
    if (activitiesLength - 1 === index) {
      breakRow = generateBreakRow('End of day', times[i], data);
    }
    i++;
    arr.push(breakRow);
  }
  return arr;
};

export const generateDoc = docData => {
  let generatedData = [...docData];
  const biggestDayActivitiesCount = generatedData
    .map(el => el.activities.length)
    .reduce((acc, el) => (acc >= el ? acc : el), 0);

  if (
    !docData.every(el => el.activities.length === biggestDayActivitiesCount)
  ) {
    generatedData = docData.map(el => {
      if (el.activities.length === biggestDayActivitiesCount) {
        return el;
      }
      return {
        ...el,
        activities: addMissing(el.activities, biggestDayActivitiesCount)
      };
    });
  }

  const doc = new Document();
  const table = new Table({
    borders: {
      top: {
        style: 'single',
        size: 5,
        color: '#000'
      },
      bottom: {
        style: 'single',
        size: 5,
        color: '#000'
      },
      left: {
        style: 'single',
        size: 5,
        color: '#000'
      },
      right: {
        style: 'single',
        size: 5,
        color: '#000'
      }
    },
    rows: [headerRow(generatedData), ...generateAllRows(generatedData)]
  });

  doc.addSection({
    children: [table]
  });
  Packer.toBlob(doc).then(blob => {
    saveAs(blob, 'course-designer.docx');
  });
};
