<template>
  <div class="c-step-2">
    <div class="c-step__title c-step__title--step-2">
      {{ $t('Select activities and exercises') }}
    </div>
    <div class="c-step__sub-title">
      {{ $t('from the list provided for each topic') }}
    </div>
    <TopicList
      :valuesToSelect="activities"
      v-model="selectedActivities"
      :duration="duration"
      days
    />
  </div>
</template>

<script>
import TopicList from './TopicList';
export default {
  name: 'Step2',
  props: ['activities', 'value', 'duration'],
  components: {
    TopicList
  },
  computed: {
    selectedActivities: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit('input', val);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.c-step__title--step-2 {
  margin-bottom: 0;
}
.c-step-2 {
  padding-bottom: 50px;
}
</style>
